import { connect } from "react-redux";
import Forms from "../components/Forms";
import Board from "../components/Board";
import Summarise from "../components/Summarise";
import UsersCard from "../components/UsersCard";
import HistoryUser from "../components/HistoryUser";
import { addUser, editUser, deleteUser, updateUser, fetchUsers, searchUser, fetchOneUser, countUser } from "../actions/Users";
import { addSpending, countSpending } from "../actions/Spending";
import { fetchStatus } from "../actions/Statuses";
import { fetchGroups } from "../actions/Groups";

const mapStateToProps = (state) => {
  return {
    error: state.Users.error,
    items: state.Users.items,
    pending: state.Users.pending,
    editing: state.Users.editing,
    statuses: state.Statuses.statuses,
    groups: state.Groups.groups,
    word: state.Users.word,
    amountUsers: state.Users.amountUsers,
    amountSpending: state.Spending.amountSpending
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    dispatchFetchUsers: () => {
      dispatch(fetchUsers());
    },
    dispatchAddUser: (data) => {
      dispatch(addUser(data));
    },
    dispatchEditUser: (id) => {
      dispatch(editUser(id));
    },
    dispatchFetchStatuses: () => {
      dispatch(fetchStatus());
    },
    dispatchFetchGroups: () => {
      dispatch(fetchGroups());
    },
    dispatchDeleteUser: (id) => {
      dispatch(deleteUser(id));
    },
    dispatchUpdateUser: (id, data) => {
      dispatch(updateUser(id, data));
    },
    dispatchSearcheUser: (word) => {
      dispatch(searchUser(word));
    },
    dispatchFetchOneUsers: (id) => {
      dispatch(fetchOneUser(id));
    },
    dispatchCountUser: () => {
      dispatch(countUser());
    },
    dispatchAddSpending: (data) => {
      dispatch(addSpending(data));
    },
    dispatchCountSpending: () => {
      dispatch(countSpending());
    },
  };
};

const FormsWithLogic = connect(mapStateToProps, mapDispatchToProps)(Forms);
const BoardWithLogic = connect(mapStateToProps, mapDispatchToProps)(Board);
const UsersCardWithLogic = connect(mapStateToProps, mapDispatchToProps)(UsersCard);
const SummariseWithLogic = connect(mapStateToProps, mapDispatchToProps)(Summarise);
const HistoryUserWithLogic = connect(mapStateToProps, mapDispatchToProps)(HistoryUser);
export default {
  FormsWithLogic,
  BoardWithLogic,
  UsersCardWithLogic,
  SummariseWithLogic,
  HistoryUserWithLogic,
};
