import React, { useEffect, useState } from "react";
import Title from "./shared/Title";
import FormSearch from "./shared/FormSearch";
import Table from "./shared/Table";

const HistoryUser = (props) => {
  //   console.log("HistoryUser", props);
  const { firstName, lastName, nickName, spending } = props.items;
  const [sumPrice, setSumPrice] = useState(0);
  const columns = ["ลำดับ", "วันที่"];

  const handleChange = (e) => {
    console.log(e.target.value);
    props.dispatchSearcheUser(e.target.value);
    if (e.target.value === "") props.dispatchFetchUsers();
  };

  const caculateService = (amount) => {
    return amount * 100;
  };

  useEffect(() => {
    if (spending != undefined) {
      setSumPrice(caculateService(spending.length));
    }
  }, [props.items]);

  return (
    <React.Fragment>
      <Title name={`ประวัติการเข้าสนาม`} />
      <div className="flex flex-wrap mb-6 mt-3">
        <div className="container mx-5 py-3">
          <input
            className="w-full h-16 px-3 rounded mb-8 focus:outline-none focus:shadow-outline text-xl px-8 shadow-lg"
            type="text"
            value={`คุณ ${nickName} ${firstName} ${lastName}`}
            disabled></input>
          <input
            className="w-full h-16 px-3 rounded mb-8 focus:outline-none focus:shadow-outline text-xl px-8 shadow-lg"
            type="text"
            value={`ยอดรวมค่าใช้บริการ ${sumPrice} บาท`}
            disabled></input>
        </div>
      </div>
      <div className="ml-6 overflow-x-auto">
        <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
          <Table columns={columns} props={props} />
        </div>
      </div>
    </React.Fragment>
  );
};

export default HistoryUser;
