import React from "react";
import Colume from "./Colume";
import RowHistory from "./RowHistory";
import RowsUser from "./RowsUser";
const Table = ({ columns, props }) => {
  console.log(`table`, props);

  return (
    <table className="sm:min-w-min md:min-w-auto leading-normal">
      <thead>
        <tr>
          {columns.map((name, i) => (
            <Colume key={i} name={name} />
          ))}
        </tr>
      </thead>
      <tbody>
        <LoadRow list={props.items} props={props} />
      </tbody>
    </table>
  );
};

const LoadRow = ({ list, props }) => {
  if (!Array.isArray(list) && !list.spending ) {
    return null;
  }
  if (list.spending) {
    return (
      <React.Fragment>
        {list.spending.map((spending, i) => (
          <RowHistory key={i} spending={spending} props={props} index={i} />
        ))}
      </React.Fragment>
    );
  } else
    return (
      <React.Fragment>
        {list.map((user, i) => (
          <RowsUser key={i} user={user} props={props} />
        ))}
      </React.Fragment>
    );
};
export default Table;
