import React from "react";
import ButtonArmy from "../shared/ButtonArmy";
// import ModalReact from "react-modal";
// ModalReact.setAppElement("#root");
import Modal from "../shared/Modal";
import moment from "moment";
const RowHistory = ({ spending, props, index }) => {
  const [modalIsOpen, setIsOpen] = React.useState(false);

  const openModal = () => setIsOpen(true);
  const afterOpenModal = () => {};
  const closeModal = () => setIsOpen(false);

  const getLocalDateThai = (date) => {
    return new Date(date).toLocaleDateString("th-TH", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  const getTime = (date) => {
    return moment(date).format(`HH:mm:ss`);
  };

  return (
    <React.Fragment>
      <tr>
        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <p className="text-gray-900 whitespace-no-wrap">{index + 1}</p>
        </td>
        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <span className="relative inline-block px-3 py-1 font-semibold text-green-900 leading-tight">
            <span aria-hidden className="absolute inset-0 bg-green-200 opacity-50 rounded-full"></span>
            <span className="relative">
              {getLocalDateThai(spending.createdAt)} {getTime(spending.createdAt)}
            </span>
          </span>
        </td>

        {/* <ButtonArmy value="ลบ" color="red" OnClick={openModal} /> */}
      </tr>
      {/* <Modal isOpen={modalIsOpen} onAfterOpen={afterOpenModal} onRequestClose={closeModal} handle={handleDeleteClick} userId={user.id} /> */}
    </React.Fragment>
  );
};

export default RowHistory;
