import {
  SPENDING,
  FETCH_SPENDING_PENDING,
  FETCH_SPENDING_SUCCESS,
  FETCH_SPENDING_ERROR,
  ADD_SPENDING_SUCCESS,
  COUNT_SPENDING_SUCCESS,
} from "../constants/Spending";
import generateAction from "../hoc/generateAction";
import getActionGroup from "../utils/getActionGroup";
const fetchSpendingPending = () => {
  return {
    type: FETCH_SPENDING_PENDING,
  };
};

const fetchSpendingError = (error) => {
  return {
    type: FETCH_SPENDING_ERROR,
    error: error,
  };
};

const addSpendingSuccess = () => {
  return {
    type: ADD_SPENDING_SUCCESS,
    editing: true,
  };
};

const countSpendingSucess = (amountSpending) => {
  return {
    type: COUNT_SPENDING_SUCCESS,
    amountSpending: amountSpending,
  };
};
const headers = {
  "Content-Type": "application/json",
};

const actionGruop = getActionGroup(fetchSpendingPending, fetchSpendingError);
export const addSpending = (data) => generateAction(SPENDING, "POST", addSpendingSuccess, actionGruop, ``, JSON.stringify(data), headers);

export const countSpending = () => generateAction(SPENDING, "GET", countSpendingSucess, actionGruop, `/`);
