import React from "react";

const Form = ({ label, type, name, register, errors, onChange = null }) => {
  return (
    <div className="w-full md:w-full px-3 md:mb-0 mt-3">
      <label className="block uppercase tracking-wide text-gray-700 font-bold text-xs mb-2">{label}</label>
      <input
        className="appearance-none block w-full p-1 shadow rounded-lg bg-gray-100 outline-none focus:bg-gray-200"
        type={type}
        name={name}
        ref={register}
        onChange={onChange}
      />
      <span className="msg-error">{errors && errors.message}</span>
    </div>
  );
};

export default Form;
