import React, { useEffect } from "react";
import Title from "./shared/Title";
import FormSearch from "./shared/FormSearch";
import Table from "./shared/Table";

const Board = (props) => {
  const columns = ["ลำดับและชื่อ", "อายุ", "เบอร์โทร", "จำนวนครั้งที่ใช้สนาม","Action"];
  const handleChange = (e) => {
    console.log(e.target.value);
    props.dispatchSearcheUser(e.target.value);
    if (e.target.value === "") props.dispatchFetchUsers();
  };

  useEffect(() => {
    props.dispatchFetchUsers();
  }, []);
  return (
    <React.Fragment>
      <Title name="Table" />
      <div className="flex flex-wrap mb-6 mt-3">
        {/* <SelectBox label="Max Row" test={[5, 10, 20]} />
        <SelectBox label="Filter" test={["Active", "Inactive"]} /> */}
        <FormSearch handleChange={handleChange} />
      </div>
      <div className="md:ml-12 overflow-x-auto">
        <div className="inline-block sm:min-w-auto md:min-w-full shadow rounded-lg overflow-hidden">
          <Table columns={columns} props={props} />
        </div>
      </div>
    </React.Fragment>
  );
};

export default Board;
