import React, { useState } from "react";
import ButtonArmy from "../shared/ButtonArmy";
// import ModalReact from "react-modal";
// ModalReact.setAppElement("#root");
import Modal from "../shared/Modal";
import FormData from "form-data";
import ColorText from "../shared/colorText";
const RowsUser = ({ user, props }) => {
  const [modalIsOpen, setIsOpen] = React.useState(false);

  const openModal = () => setIsOpen(true);
  const afterOpenModal = () => {};
  const closeModal = () => setIsOpen(false);

  const handleEditClick = async (id) => {
    props.dispatchEditUser(id);
    props.history.push(`/form/${id}/edit`);
  };
  const handleDeleteClick = async (id) => {
    await props.dispatchDeleteUser(id);
    setTimeout(() => {
      props.dispatchFetchUsers();
    }, 200);
  };
  const handleSeeHistory = async (id) => {
    props.dispatchEditUser(id);
    props.history.push(`/history/${id}`);
  };
  const handleAddSpending = async (id) => {
    const data = {
      userId: id,
    };
    props.dispatchAddSpending(data);
    props.dispatchFetchUsers();
  };
  return (
    <React.Fragment>
      <tr>
        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <div className="flex items-center">
            <div className="flex-shrink-0 w-10 h-10">{user.id}</div>
            <div className="ml-3">
              <p className="text-gray-900 whitespace-no-wrap">
                {user.nickName} {user.firstName} {user.lastName}
              </p>
            </div>
          </div>
        </td>

        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <p className="text-gray-900 whitespace-no-wrap">{user.Age}</p>
        </td>
        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <span className="relative inline-block px-3 py-1 font-semibold text-black-900 leading-tight">
            <span aria-hidden className="absolute inset-0 bg-black-200 opacity-50 rounded-full"></span>
            <span className="relative">{user.Tel}</span>
          </span>
        </td>
        <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <SpendingColor len={user.spending.length} msg={`${user.spending.length}`} />
        </td>
        <td className="inline-flex px-5 py-5 border-b border-gray-200 bg-white text-sm">
          <div className="w-1/3 p-1">
            <ButtonArmy value={<i class="fas fa-exchange-alt fa-lg"></i>} color="blue" OnClick={() => handleAddSpending(user.id)} />
          </div>
          <div className="w-1/3 p-1">
            <ButtonArmy value={<i class="fas fa-history fa-lg"></i>} color="teal" OnClick={() => handleSeeHistory(user.id)} />
          </div>
          <div className="w-1/3 p-1">
            <ButtonArmy value={<i class="fas fa-edit fa-lg"></i>} color="yellow" OnClick={() => handleEditClick(user.id)} />
          </div>
          <div className="w-1/3 p-1">
            <ButtonArmy value={<i class="fas fa-trash-alt fa-lg"></i>} color="red" OnClick={openModal} />
          </div>
        </td>
      </tr>
      <Modal isOpen={modalIsOpen} onAfterOpen={afterOpenModal} onRequestClose={closeModal} handle={handleDeleteClick} userId={user.id} />
    </React.Fragment>
  );
};

const SpendingColor = ({ len, msg }) => {
  if (len <= 3) return <ColorText color="blue" msg={msg} />;
  else if (len <= 4) return <ColorText color="purple" msg={msg} />;
  else if (len <= 5) return <ColorText color="orange" msg={msg} />;
  else if (len <= 6) return <ColorText color="yellow" msg={msg} />;
  else if (len >= 7) return <ColorText color="red" msg={msg} />;
};

export default RowsUser;
