import {
  SPENDING,
  FETCH_SPENDING_PENDING,
  FETCH_SPENDING_SUCCESS,
  FETCH_SPENDING_ERROR,
  ADD_SPENDING_SUCCESS,
  COUNT_SPENDING_SUCCESS,
} from "../constants/Spending";

const initialState = {
  pending: false,
  items: [],
  error: `no error`,
  editing: false,
};
const Spending = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_SPENDING_PENDING:
      return {
        ...state,
        pending: true,
        items: [],
      };
    case FETCH_SPENDING_ERROR:
      return {
        ...state,
        items: [],
        error: action.error,
      };
    case FETCH_SPENDING_SUCCESS:
      return {
        ...state,
        items: action.items,
      };
    case ADD_SPENDING_SUCCESS:
      return {
        ...state,
      };
    case COUNT_SPENDING_SUCCESS:
      return {
        ...state,
        amountSpending: action.amountSpending,
      };
    default:
      return state;
  }
};
export default Spending;
